@import "../../assets/styles/global.module.scss";

@mixin fonts($font-family, $size: 16px, $color, $bold: 700) {
    font-family: $font-family, sans-serif;
    font-weight: $bold;
    font-size: $size;
    color: $color;
}
.productItem {
    // width: 100%;
    height: 100%;
    text-align: left;
    // padding: 0 1.2% 0 1.2%;
    padding: 10px;
    border: 0.6px solid rgb(243, 242, 242);
    background: white;
    // overflow: hidden;
    padding-bottom: 20px;
    position: relative;
    transition: 0.35s ease;
    overflow: visible;
    .btnCartContainer,
    .iconFav {
        display: none;
        opacity: 0;
    }
    .available {
        @include fonts("Poppins", 10px, #78a962, 400);
        line-height: 21px;
        margin: 10px 0 0 15px;
        display: flex;
        .iconAvailable {
            width: 10px;
            height: 10px;
            margin: 5px;
        }
    }
    .notAvaliable {
        color: #c94d3f;
    }
    .iconFav {
        width: 30px;
        height: 30px;
        position: absolute;
        right: 26px;
        top: 15px;
        cursor: pointer;
        // z-index: 3;
    }
    .imageContainer {
        height: 150px;
    }
    .imgProduct {
        transition: 1s ease-in-out;
        display: block;
        max-height: 150px;
        // max-width: 150px;
        max-width: 65%;
        margin: 5px auto;
        margin-top: 20px;
    }
    .imgProduct:hover {
        transform: scale(1.1);
    }
    .productName {
        @include fonts("Poppins", 13px, black, 400);
        color: #39404b;
        margin: 0;
        margin-top: 15px;
    }
    .productModel {
        @include fonts("Poppins", 12x, black, 400);
        color: #39404b;
        margin: 0;
    }
    .previousPrice {
        @include fonts("Poppins", 14px, #666666, 400);
        line-height: 19.6px;
        margin-top: 10px;
        display: block;
        text-decoration: line-through;
    }
    .price {
        @include fonts("Poppins", 18px, black, 600);
        line-height: 25px;
        display: block;
    }
    .btnCartContainer {
        bottom: 0;
        left: 0;
        background-color: #fff;
        position: absolute;
        margin-bottom: -40px;
        margin-left: -0.6px;
        z-index: 5;
        width: 100%;
        border: 0.6px solid rgb(243, 242, 242);
        border-top: none;
        box-shadow: 0 20px 35px rgb(0, 0, 0, 0.17);
        padding-bottom: 10px;
        .btnCart {
            width: 160px;
            margin: 10px auto;
            display: flex;
            margin-bottom: 0;
            background-color: white;
            border: 2px solid #0156ff;
            box-sizing: border-box;
            border-radius: 50px;
            padding: 8px 20px;
            cursor: pointer;
            img {
                width: 19px;
                margin: 2px 10px 2px 0;
            }
            svg {
                width: 19px;
                margin: 1px 10px 2px 0;
            }
            p {
                @include fonts("Poppins", 14px, #0156ff, 600);
                margin-top: 3px;
            }
        }
        .btnCart:active {
            background-color: #0156ff;
            // transition: 0.2s linear;
            svg path {
                fill: white;
            }
            p {
                color: white;
            }
        }
        .btnInCart {
            border: 2px solid #c94d3f;
            cursor: default;
            img {
                display: none;
            }
            p {
                @include fonts("Poppins", 14px, #c94d3f, 600);
                margin: 2px auto;
            }
        }
    }
}
.linksToCardPage {
    text-decoration: none;
}
.productItem:hover {
    z-index: 5;
    box-shadow: $shadow-3;
    transform: scale(1.05);
    .iconFav,
    .btnCartContainer {
        display: flex;
        opacity: 1;
        transition: 1s;
    }
    .btnCartContainer {
        position: absolute;
        .btnCart {
            display: flex;
        }
    }
}

